import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import { useLocation, useNavigate } from 'react-router-dom'; // useNavigate 
import TranscriptionSection from './TranscriptionSection';
import UploadModal from './UploadModal';
import Calendar from './Calendar'; // Calendar  import

const SidebarContainer = styled.div`
  width: 400px;
  background-color: #f0f0f0;
  padding: 20px;
  position: absolute;
  right: 10px;
  top: 15px;
  bottom: 10px;
  height: calc(100vh - 10px);
  display: flex;
  flex-direction: column;
  border-radius: 10px 30px 30px 10px;
  box-sizing: border-box;

  ::-webkit-scrollbar {
    width: 0px;
  }
`;

const Tabs = styled.div`
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
`;

const Tab = styled.button`
  background: none;
  border: none;
  position: relative;
  padding: 10px;
  font-size: 14px;
  cursor: pointer;
  color: #333;
  font-weight: normal;
  flex: 1;

  &::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: ${(props) => (props.$active ? '#0281F2' : 'transparent')};
  }

  &:hover::after {
    background-color: ${(props) => (props.$active ? '#0281F2' : '#ddd')};
  }

  ${(props) => props.$active && `
    color: #4285F4;
    font-weight: bold;
  `}
`;

const TabContent = styled.div`
  flex: 4;
  overflow-y: auto;
`;

const UploadSection = styled.div`
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
`;

const CalendarSection = styled.div`
  align-items: center;
  justify-content: center;
`;

const MeetingListSection = styled.div`
  flex: 3;
  overflow-y: auto;
`;

const SectionTitle = styled.h2`
  font-size: 20px;
  margin-bottom: 15px;
  color: #05305B;
`;

const MeetingList = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
`;

const MeetingItem = styled.li`
  background-color: #fff;
  padding: 15px;
  border-radius: 15px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  cursor: pointer;
`;

const MeetingTitle = styled.span`
  font-weight: regular;
  color: #05305B;
  font-size: 20px;
`;

const MeetingDate = styled.span`
  font-size: 14px;
  color: #666;
  margin-left: 10px;
`;

const StyledParagraph = styled.p`
  font: Roboto Condensed;
  &:first-child {
    margin-top: 30px;
    font-size: 20px;
    color: #05305B;
    font-weight: bold;
  }
  &:last-child {
    font-size: 20px;
    font-weight: regular;
  }
`;

const SectionDivider = styled.div`
  width: 100%;
  height: 2px;
  background-color: #e0e0e0;
  margin: 10px 0;
`;

const UBTNSidebar = styled.button`
  background: none;
  border: 2px solid #0073e6;
  border-radius: 10px;
  padding: 20px;
  cursor: pointer;
  font-size: 16px;
  color: #0073e6;
  margin-top: 10px;
  align-self: flex-end;
`;

const RightSidebar = ({ userId }) => {
  const [activeTab, setActiveTab] = useState('upload');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [meetings, setMeetings] = useState([]);
  const [selectedMeetingId, setSelectedMeetingId] = useState(null);
  const location = useLocation();
  const navigate = useNavigate();  // useNavigate  

  const fetchMeetings = async () => {
    try {
      const response = await axios.get('http://localhost:8000/upload/listMeetings', {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('access_token')}` //   
        }
      });
      setMeetings(response.data.meetings);
    } catch (error) {
      console.error('Error fetching meetings:', error);
    }
  };

  useEffect(() => {
    fetchMeetings();
  }, [location]);

  const handleUploadClick = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const handleUpload = (transcription) => {
    setSelectedMeetingId(transcription.id);
    setIsModalOpen(false); //  
    navigate(`/upload/meeting/${transcription.id}`);  //   
  };

  const handleBackClick = () => {
    setSelectedMeetingId(null);
    fetchMeetings();
  };

  return (
    <SidebarContainer>
      <Tabs>
        <Tab $active={activeTab === 'upload'} onClick={() => setActiveTab('upload')}></Tab>
        <Tab $active={activeTab === 'calendar'} onClick={() => setActiveTab('calendar')}></Tab>
      </Tabs>
      <TabContent>
        {activeTab === 'upload' && selectedMeetingId ? (
          <TranscriptionSection meetingId={selectedMeetingId} onBackClick={handleBackClick} />
        ) : (
          <>
            {activeTab === 'upload' ? (
              <UploadSection>
                <StyledParagraph>      !</StyledParagraph>
                <StyledParagraph>          </StyledParagraph>
                <UBTNSidebar onClick={handleUploadClick}> </UBTNSidebar>
              </UploadSection>
            ) : (
              <CalendarSection>
                <Calendar />
              </CalendarSection>
            )}
            {activeTab === 'upload' && (
              <MeetingListSection>
                <SectionDivider />
                <SectionTitle> </SectionTitle>
                {Array.isArray(meetings) && meetings.length === 0 ? (
                  <StyledParagraph>   !</StyledParagraph>
                ) : (
                  <MeetingList>
                    {Array.isArray(meetings) && meetings.map((meeting, index) => (
                      <MeetingItem key={index} onClick={() => {
                        setSelectedMeetingId(meeting.id);
                      }}>
                        <div>
                          <MeetingTitle>{meeting.filename.replace(/\.[^/.]+$/, "")}</MeetingTitle>
                          <MeetingDate>{meeting.meeting_date}</MeetingDate>
                        </div>
                      </MeetingItem>
                    ))}
                  </MeetingList>
                )}
              </MeetingListSection>
            )}
          </>
        )}
      </TabContent>
      <UploadModal isOpen={isModalOpen} onClose={closeModal} onUpload={handleUpload} /> {/*   */}
    </SidebarContainer>
  );
};

export default RightSidebar;