import React from 'react';
import styled from 'styled-components';
import nexochatImage from '../../images/nexochat.png';
import userPlaceholder from '../../images/user.png';
import { useUser } from '../../contexts/UserContext';
import { useNavigate } from 'react-router-dom';

//  
const SidebarContainer = styled.div`
  width: 175px;
  background-color: #3788B4;
  left: 10px;
  top: 15px;
  bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  border-radius: 30px 10px 10px 30px;
  position:fixed;
`;

const UserProfile = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: auto;
  margin-bottom: 50px;
`;

const UserProfileImage = styled.img`
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-bottom: 10px;
`;

const NexoLogo = styled.img`
    width: 135px;
    height: 104px;
    margin-bottom: 20px;
    margin-top: 50px;
`;
const LeftSidebar = () => {
  const navigate = useNavigate(); //    
  const { user } = useUser(); //   


  //       
  const handleLogoClick = () => {
    navigate('/');
  };

  return (
    <SidebarContainer>
        {/* NexoChat       */}
        <NexoLogo src={nexochatImage} alt="NexoChat Logo" onClick={handleLogoClick} style={{ cursor: 'pointer' }} />
        {/*    */}
        <UserProfile>
          <UserProfileImage src={user ? user.imageUrl : userPlaceholder} alt="User" />
          <span>{user ? user.name : 'USER'}</span>
        </UserProfile>

    </SidebarContainer>
  );
};

//   
export default LeftSidebar;


